*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  line-height: 1.7;
  background-color: blanchedalmond;
}

#country-name {
  min-width: 100px;
  margin-left: 20px;
  height: 20px;
  border: 2px solid black;
}

.container-js {
  display: flex;
  flex-wrap: wrap;
}

.country-container {
  display: block;
}

.country-title {
  text-align: center;
  font-size: 24px;
}

.country-content {
  display: flex;
}

img {
  display: block;
  width: 540px;
  height: auto;
  margin-left: 50px;
}

.text {
  font-size: 16px;
  font-weight: 600;
}
